.nav-container{height:100%;width:15%;position:fixed;z-index:1;overflow-x:hidden;box-shadow:0 8px 20px 0 rgba(0,0,0,0.6)}.nav-container .name-container{text-align:center;color:black;padding:20px 6px;border-bottom:1px solid black}.nav-container .name-container .nav-image img{width:80%;border-radius:50%}.nav-container .name-container .nav-occupation{font-size:16px}.nav-container .name-container .nav-name{font-family:'Helvetica';font-size:30px}.nav-container .nav-category{line-height:1.5;padding:16px 8px 6px 16px;text-decoration:none;font-size:20px;color:black;display:block}.nav-container .nav-category .material-icons{font-size:30px}.nav-container .nav-category .nav-description{padding-left:10px;margin-bottom:50px}.nav-container .nav-category:hover{padding-left:20px;border-left:4px solid black;background-color:#cccccc;cursor:pointer}a{text-decoration:none}a:visited{color:black}
